.form-field {
  font-family: Poppins, serif;
  box-sizing: border-box;
  display: block;

  height: 48px;
  width: 100%;

  border: 1px solid transparent;
  border-radius: 40px;
  padding: 14px 16px;
  color: #292929;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  background-color: #f8f8fa;

  &::placeholder {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #7e7e83;
  }

  &:hover {
    border-color: #d5d7de;
  }

  &:focus {
    border-color: #292929;
    outline: none;
  }

  &:-webkit-autofill {
    -webkit-text-fill-color: #292929;
    border: 1px solid transparent;
    -webkit-box-shadow: 0 0 0 1000px #f8f8fa inset;
    box-shadow: 0 0 0 1000px #f8f8fa inset;
    font-family: Poppins, serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
  }

  &--textarea {
    font-family: inherit;
    height: 148px;
    border-radius: 16px;
    resize: none;
  }
}
